.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }

    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &__bg {
        background: rgba(#0A4140, 0.60);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        background: #006665;
        width: 37.5em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 6.25em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: 0;
        right: 0;
    }
    .text66{
        text-align: center;
        color: #fff;
    }
    .consult-form{
        grid-template-columns: 100%;
        margin-top: 1.875em;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            padding: 3em 1em;
        }
    }
}
