.text66{
  font-size: 4.125em;
  line-height: 130%;
  text-transform: uppercase;
  font-family: $font2;
  span{
    color: $primary;
  }
}

.text24{
  font-size: 1.5em;
  line-height: 130%;
}

.text18{
  font-size: 1.125em;
  line-height: 160%;
}

.text14{
  font-size: .875em;
  line-height: 160%;
  letter-spacing: 0.0875em;
}

.dark-text{
  color: $dark;
}

.light-text{
  font-weight: 300;
}

.font2{
  font-family: $font2;
}


@media screen and (max-width: 992px){
  .text66{
    font-size: 1.75em;
  }

  .text24{
    font-size: 1em;
  }

  .text18{
    font-size: .875em;
  }
}
