.main{
  position: relative;
  color: #fff;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background: linear-gradient(180deg, rgba(26, 25, 31, 0.80) 0%, rgba(34, 33, 41, 0.00) 58.33%);
    width: 100%;
    height: 100%;
  }
  &__hint{
    position: absolute;
    left: 4em;
    bottom: 5.25em;
    width: 17.5em;
    height: 17.5em;
    background: #232229;
    border-radius: 50%;
    font-family: $font2;
    @include center;
    color: #FFF;
    padding: 2.16em;
    text-align: center;
    span{
      font-size: 1.25em;
      line-height: 130%;
    }
  }
  &__bg{
    width: 100%;
    display: block;
  }
  &__wrap{
    padding: 10.37em 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 3;
  }
  &__top{
    font-family: $font2;
    text-transform: uppercase;
    text-align: center;
    @include grid100gap(1.25em);
  }
  &__title{
    font-size: 5.375em;
    line-height: 130%;
    //font-weight: 300;
  }
  &__desc{
    font-size: 2.625em;
    line-height: 130%;
  }
}

.consult{
  margin-top: -8.38em;
  position: relative;
  z-index: 2;
  &__inner{
    padding: 6.25em 3.125em;
    background-color: $primary;
  }
  &__top{
    color: #fff;
    width: 62.5em;
    max-width: 100%;
    text-align: center;
    margin: 0 auto 3.75em;
    @include grid100gap(1.25em);
    .text24{
      text-transform: uppercase;
      font-family: $font2;
    }
  }
}

.consult-form{
  display: grid;
  grid-template-columns: repeat(5,calc(20% - 1em));
  gap: 1.25em;
  &__btn{
    width: 100%;
    height: 4.25em;
    border: 1px solid #FFF;
    text-transform: uppercase;
    font-weight: 500;
    color: #fff;
    &:hover{
      background-color: #fff;
      color:  $primary;
    }
    span{
      font-size: 1.125em;
      line-height: 140%;
      letter-spacing: 0.1125em;
    }
  }
}

.portfolio{
  padding: 9.38em 0;
  overflow: hidden;
  position: relative;
  &__num{
    opacity: .6;
    top: 4em;
    left: 50%;
    transform: translateX(-50%);
    position: absolute;
    width: 79em;
    pointer-events: none;
  }
  .text66{
    text-align: center;
  }
  .container{
    position: relative;
    z-index: 2;
  }
}

.portfolio-slider{
  position: relative;
  margin: 3.75em auto 0;
  width: 71.25em;
  max-width: 100%;
  .swiper{
    &-container{
      width: calc(100% + 14.75em);
    }
    &-slide{
      transition: all .3s;
      width: 100%;
      padding-right: 14.75em;
      opacity: .5;
      &-active{
        opacity: 1;
      }
    }
    &-pagination{
      position: absolute;
      left: 100%;
      bottom: unset;
      top: 50%;
      transform: translateY(-50%);
      display: flex;
      flex-direction: column;
      margin-left: 7em;
      &-bullet{
        margin: .3125em 0 !important;
        width: 0.625em;
        height: 0.625em;
        background: #E2E2E2;
        opacity: 1;
        border-radius: 0;
        &-active{
          background: $primary;
        }
      }
    }
    &-pagination2{
      position: absolute;
      right: 100%;
      top: 50%;
      transform: translateY(-50%);
      margin-right: 5.5em;
    }
  }
}

.custom-pagination{
  display: flex;
  white-space: nowrap;
  font-family: $font2;
  @include sameNumWidth;
  &__current{
    font-size: 6.25em;
    line-height: 130%;
    color: $primary;
  }
  &__total{
    position: absolute;
    bottom: -1.44em;
    left: 100%;
    margin-left: -.5em;
    color: #C2C2C2;
    &-inner{
      font-size: 2.25em;
      line-height: 130%;
    }
  }
}

.portfolio-card{
  display: grid;
  grid-template-columns: 50% 50%;
  &__item{
    position: relative;
  }
  &__img{
    @include imgCover(119%);
  }
  &__badge{
    position: absolute;
    top: 1.25em;
    left: 1.25em;
    padding: 0 1.25em;
    background-color: $primary;
    color: #fff;
    font-weight: 500;
    height: 2.5625em;
    display: flex;
    align-items: center;
    span{
      font-size: 1em;
      line-height: 130%;
    }
    &._after{
      left: unset;
      right: 1.25em;
    }
  }
}

.gallery{
  background: #F5F5F5;
  padding: 9.38em 0;
  @media screen and (min-width: 993px){
    .container{
      padding: 0 1.25em;
    }
    ._cut{
      padding-top: 14.75em;
    }
    &__title{
      position: absolute;
      top: 0;
      left: 5em;
      max-width: 53.125em;
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
    position: relative;
  }

}

.gallery-img{
  &._row2{
    grid-row: span 2;
  }
  &._col2{
    grid-column: span 2;
    .gallery-img__wrap{
      padding-bottom: 58.1%;
    }
  }
  &__wrap{
    display: block;
    min-height: 100%;
    @include imgCover(85.7%);
    &:hover{
      &:before{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-image: url(../img/plus.svg),linear-gradient(0deg, rgba(0, 102, 101, 0.50) 0%, rgba(0, 102, 101, 0.50) 100%);
      background-repeat: no-repeat;
      background-size: 3.125em auto, cover;
      background-position: center;
      transition: all .3s;
      opacity: 0;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 5em 0 7em;
    min-height: unset;
    &:before{
      background: linear-gradient(180deg,rgba(26,25,31,.8) 0,rgba(34,33,41,.5) 58.33%);
    }
    &__title{
      font-size: 2em;
    }
    &__desc{
      font-size: 1.125em;
    }
    &__hint{
      display: none;
    }
  }

  .consult{
    margin: 0;
    background-color: $primary;
    &__inner{
      padding: 2em 0;
    }
    &__top{
      margin-bottom: 1.25em;
      gap: 1em;
    }
  }

  .consult-form{
    grid-template-columns: 100%;
    gap: 1em;
  }

  .portfolio{
    padding: 2em 0;
    text-align: center;
    &__num{
      display: none;
    }
  }

  .portfolio-slider{
    margin-top: 1em;
    .swiper{
      &-container{
        width: calc(100% + 1em);
      }
      &-slide{
        width: 90%;
        padding-right: 1em;
      }
      &-pagination{
        display: none;
      }
      &-pagination2{
        position: static;
        margin: 1em 0 0;
        transform: unset;
        display: inline-flex;
        justify-content: center;
        align-items: flex-end;
      }
    }
  }

  .custom-pagination{
    &__total{
      position: static;
      margin-left: 0;
      &-inner{
        font-size: 1em;
      }
    }
    &__current{
      font-size: 2em;
    }
  }

  .gallery{
    padding: 2em 0;
    &__title{
      grid-column: 1/-1;
      margin-bottom: .5em;
    }
    &__inner{
      grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
      gap: .5em;
    }
  }

  .gallery-img{
    &__wrap{
      &:before{
        background-size: 2em auto,cover;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .portfolio-slider{
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 100%;
        padding-right: .5em;
      }
    }
  }

  .portfolio-card{
    &__badge{
      top: .5em;
      left: .5em;
      height: 1.625em;
      padding: 0 .5em;
      span{
        font-size: .75em;
      }
      &._after{
        right: .5em;
      }
    }
  }

  .gallery{
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .25em));    }
  }
}
