@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: #fff;
        path{
            fill: #fff;
        }
    }
}
