@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@media screen and (min-width: 993px){
  .feedback-card,
  .portfolio-card{
    &.animated{
      transition: all .6s;
      opacity: 0;
      transform: translateY(2em);
      &--active{
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  .idea-card,
  .gallery-img{
    &.animated{
      transition: all .6s;
      opacity: 0;
      transform: translateY(1em);
      &--active{
        opacity: 1;
        transform: translateY(0);
      }
    }
  }

  .portfolio-slider{
    .swiper-slide:not(.swiper-slide-active){
      .portfolio-card{
        transition-delay: .2s;
      }
    }
  }

  .feedback-slider{
    .swiper-slide:not(.swiper-slide-active){
      .feedback-card{
        transition-delay: .2s;
      }
    }
  }

}

