.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    border-bottom: 1px solid #9DB4B3;
    padding: 1.78em 1em 1em 3.38em;
    height: 4.25em;
    width: 100%;
    font-size: 1em;
    background-color: transparent;
    color: #fff;
    &::placeholder {
      color: #000;
    }
    &:focus{
      border-color: #fff;
      &~svg{
        path{
          fill: #fff;
        }
      }
    }
    &._filled,
    &:focus {
      &~label{
        top: .53em;
        span{
          font-size: .875em;
        }
      }
    }
    &._error{
      border-color: #FF6C4B;
    }
  }
  label{
    position: absolute;
    left: 3.38em;
    top: 1.16em;
    color: #9DB4B3;
    transition: all .2s;
    span{
      font-size: 1em;
      line-height: 130%;
    }
  }
  &__icon{
    position: absolute;
    top: 1.06em;
    left: .625em;
    height: 1.5em;
    width: auto;
  }
}

.rating{
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.5em;
    margin-right: .625em;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: #FF9900;
      }
      &~.rating-input__item {
        path{
          fill: #FF9900;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: #FF9900;
  }
}


@media screen and (max-width: 992px){
  .rating-input{
    &__item{
      width: 1em;
      margin-right: .25em;
    }
  }
}
