.header{
  padding: 1.5em 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 30;
  width: 100%;
  transition: transform .3s;
  &._transform{
    transform: translateY(-100%);
    background: #282721;
  }
  &._fix{
    position: fixed;
    transform: translateY(-0%);
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__right,
  &__left{
    display: flex;
    align-items: center;
  }
  &__logo{
    position: relative;
    z-index: 12;
    display: block;
    width: 7.8125em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    span{
      font-size: 1em;
      line-height: 130%;
      letter-spacing: 0.1em;
    }
  }
  &._open{
    .menu{
      transform: translateX(0);
      &__bg{
        display: block;
      }
    }
    .burger{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.header__btn,
.burger{
  padding: 0 1.875em;
  height: 3.875em;
  text-transform: uppercase;
  font-weight: 300;
}

.burger{
  position: relative;
  z-index: 12;
  font-family: $font2;
  margin-left: 12.19em;
  svg{
    width: 1.5em;
    height: auto;
    @include centerAbsolute;
  }
  &__icon{
    position: relative;
    margin-right: 1.25em;
    flex-shrink: 0;
    width: 1.5em;
    height: 1.5em;
  }
  ._view2{
    opacity: 0;
  }
  &__title{
    letter-spacing: 0.1125em;
    font-size: 1.125em;
    line-height: 140%;
  }
}

.header-time{
  @include grid100gap(.12em);
  margin-right: 11.56em;
  color: #fff;
  .text18,
  .text14{
    line-height: 130%;
  }
  &__top{
    display: flex;
    align-items: center;
    margin-left: -1.25em;
    &:before{
      content: '';
      width: 0.625em;
      height: 0.625em;
      flex-shrink: 0;
      margin-right: .625em;
      background-color: $primary;
      border-radius: 50%;
    }
  }
}

.menu{
  position: fixed;
  top: 0;
  left: 0;
  height: $vh;
  width: 41.25em;
  background: #0A4140;
  z-index: 11;
  padding: 7em 6.25em;
  display: flex;
  align-items: center;
  transition: all .3s;
  transform: translateX(-100%);
  &__bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    opacity: 0.8;
    background: #0A4140;
    z-index: 10;
    display: none;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    text-transform: uppercase;
    color: #fff;
    font-family: $font2;
    font-weight: 300;
    @include lastM(0 0 3em);
    &:hover{
      color: #108E8D;
    }
    span{
      font-size: 4.125em;
      line-height: 140%;
      letter-spacing: 0.1em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__logo{
      width: 5em;
    }
    &__btn{
      margin-left: 2em;
      height: 3em;
      padding: 0 1.25em;
      span{
        font-size: .875em;
      }
    }
  }

  .burger{
    margin-left: 2em;
    width: 3em;
    height: 3em;
    padding: 0;
    &__icon{
      margin: 0;
    }
    &__title{
      display: none;
    }
  }

  .header-time{
    display: none;
  }

  .menu{
    padding: 4em 1em;
    width: 15em;
    overflow-y: auto;
    &__link{
      @include lastM(0 0 1.5em);
      span{
        font-size: 1.5em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__btn{
      margin-left: .5em;
    }
  }
}
