.comfort{
  padding: 9.38em 0 0;
  .text66{
    padding: 0 1em;
    text-align: center;
    span{
      font-weight: 400;
    }
  }
}

.house{
  position: relative;
  margin-top: 3.75em;
  &__img{
    width: 100%;
    display: block;
  }
}


.house-point{
  position: absolute;
  z-index: 2;
  &__btn{
    width: 2.875em;
    height: 2.875em;
    position: relative;
    z-index: 2;
    img{
      width: 1em;
    }
    &:not(:hover){
      background: #399E9D;
    }
  }
  &._left{
    .house-info{
      left: unset;
      right: 1.44em;
    }
  }
  &._active{
    .house-info{
      opacity: 1;
      pointer-events: unset;
      &__bg{
        display: block;
      }
    }
  }
}

.house-info{
  position: absolute;
  bottom: 1.44em;
  left: 1.44em;
  width: 18.75em;
  padding: 1.875em 3.125em;
  background: rgba(0, 102, 101, 0.80);
  backdrop-filter: blur(10px);
  color: #fff;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__icon{
    height: 3.125em;
    display: block;
    margin-bottom: .625em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 160%;
    color: #fff;
  }
  &__bg{
    display: none;
  }
  .close-btn{
    display: none;
  }
}

.close-btn{
  @include center;
  background: #108E8D;
  width: 4em;
  height: 4em;
  svg{
    width: 1.5em;
    height: auto;
  }
  &:hover{
    path{
      fill: #fff;
    }
  }
}

.consult2{
  margin-top: -24em;
}

.idea{
  padding: 9.38em 0;
  position: relative;
  overflow: hidden;
  .container{
    position: relative;
    z-index: 2;
  }
  &__bg{
    opacity: .4;
    position: absolute;
    bottom: -3em;
    left: 0;
    width: 100%;
  }
  .text66{
    text-align: center;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,20%);
    margin-top: 3.75em;
  }
}

.idea-card{
  padding: 0 3.125em;
  &__icon{
    height: 6.25em;
    display: block;
    margin-bottom: 3.125em;
  }
}

.feedback{
  padding: 9.38em 0;
  background: #F5F5F5;
  overflow: hidden;
  .text66{
    text-align: center;
  }
  &__nav{
    margin-top: 1.875em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    height: 4.25em;
    max-width: 100%;
    width: 19.25em;
    text-transform: uppercase;
    span{
      font-size: 1.125em;
    }
    svg{
      width: 1.5em;
      height: auto;
      margin-left: .625em;
    }
  }
}

.feedback-slider{
  width: 25.9375em;
  max-width: 100%;
  margin: 3.75em auto 0;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      padding-right: 1.25em;
      width: 100%;
      height: auto;
      opacity: 0.5;
      &-active{
        opacity: 1;
      }
    }
    &-button{
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      width: 3.375em;
      height: 3.375em;
      svg,
      img{
        width: 1.5em;
        height: auto;
      }
      &-prev{
        left: -2.31em;
      }
      &-next{
        right: -2.31em;
      }
    }
  }
}

.feedback-card{
  padding: 3.125em;
  background-color: #fff;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__text{
    @include grid100gap(.625em);
    margin-bottom: 1.875em;
    .text14{
      text-transform: uppercase;
    }
  }
  .rating{
    margin-top: auto;
  }
}

.order{
  background-image: url(../img/home.webp);
  background-size: cover;
  background-position: center;
  .consult__inner{
    padding: 6.25em;
    width: 50%;
    margin-left: auto;
  }
  .consult-form{
    grid-template-columns: 100%;
  }
}

.contacts{
  overflow: hidden;
  margin-top: -3em;
  &__top{
    display: flex;
    justify-content: center;
  }
  &__title{
    font-size: 29.375em;
    color: #F5F5F5;
    text-transform: uppercase;
    font-family: $font2;
  }
  &__grid{
    display: grid;
    grid-template-columns: 32.1875em calc(100% - 32.1875em);
    margin-top: -3em;
    position: relative;
    z-index: 2;
  }
}

.map{
  min-height: 100%;
  height: 36.4375em;
}

.contacts-card{
  padding: 6.25em 3.125em 6.25em 6.25em;
  background-color: $primary;
  color: #fff;
  &__items{
    @include grid100gap(1.25em);
  }
  &__title{
    font-size: 1em;
    line-height: 130%;
    text-transform: uppercase;
    font-family: $font2;
    color: #66A4A3;
    font-weight: 300;
  }
  &__link{
    font-family: $font2;
    font-size: 2.25em;
    line-height: 130%;
    text-transform: uppercase;
    font-weight: 300;
    &:hover{
      color: #66A4A3;
    }
  }
}

.partners{
  padding: 3.12em 0 4.31em;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    color: #A2A2A2;
    flex-shrink: 0;
    margin-right: 1em;
  }
  &__items{
    display: flex;
    align-items: center;
  }
  &__img{
    display: flex;
    align-items: center;
    height: 6.25em;
    padding: 0 .75em;
    max-height: 27em;
    @media screen and (min-width: 993px){
      padding-top: 1em;
      padding-bottom: 1em;
    }
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
    &:not(:last-child){
      margin-right: 1.875em;
      padding-right: 1.875em;
      border-right: .3125em solid #F5F5F5;
    }
  }
}


@media screen and (max-width: 992px){
  .comfort{
    padding-top: 2em;
  }

  .house{
    margin-top: 1.25em;
  }

  .house-point{
    &._active{
      z-index: 10;
    }
    &__btn{
      position: static;
    }
  }

  .house-info{
    background: rgba(0,102,101,1);
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 21;
    padding: 2em 1em;
    text-align: center;
    backdrop-filter: unset;
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background: rgba(#0A4140,.8);
      z-index: 20;
    }
    .close-btn{
      display: flex;
      top: 0;
      right: 0;
      position: absolute;
    }
    &__icon{
      height: 3em;
      margin: 0 auto 1em;
    }
  }

  ._open-point{
    overflow-y: hidden;
    .header{
      z-index: 1;
    }
  }

  .close-btn{
    font-size: .5em;
  }

  .consult2{
    margin: 0;
  }

  .idea{
    padding: 2em 0;
    &__bg{
      display: none;
    }
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: repeat(3,calc(100% / 3 - 4em / 3));
      gap: 2em;

    }
  }

  .idea-card{
    padding: 0;
    &__icon{
      height: 3em;
      margin-bottom: .75em;
    }
  }

  .feedback{
    padding: 2em 0;
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      height: 3em;
      span{
        font-size: .875em;
      }
      svg{
        width: 1.25em;
        margin-left: .25em;
      }
    }
  }

  .feedback-slider{
    margin-top: 1.25em;
    width: 20em;
    margin-left: 0;
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        padding-right: .75em;
      }
      &-button{
        display: none;
      }
    }
  }

  .feedback-card{
    padding: 1em;
    &__text{
      margin-bottom: 1em;
    }
  }

  .order{
    .consult__inner{
      padding: 2em 1em;
      width: 100%;
    }
  }

  .contacts{
    margin: 0;
    &__title{
      display: none;
    }
    &__grid{
      margin: 0;
      grid-template-columns: 100%;
    }
  }

  .contacts-card{
    padding: 2em 1em;
    &__link{
      font-size: 2em;
    }
  }

  .partners{
    padding: 2em 0;
    &__inner{
      display: block;
    }
    &__title{
      margin: 0 0 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .house-point{
    &__btn{
      font-size: .5em;
    }
  }

  .idea{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 1em));
    }
  }

  .feedback{
    &__btn{
      width: 100%;
    }
  }

  .map{
    height: 20em;
  }

  .partners{
    &__items{
      display: block;
    }
    &__img{
      height: auto;
      padding: 0 !important;
      &:not(:last-child){
        border: none;
        margin-bottom: 1.5em;
      }
      img{
        max-height: 3em;
      }
    }
  }
}
